@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&family=Lato:ital,wght@0,400;0,700;1,400&display=swap);
body {
  margin: 0;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

title {
  font-family: "Amatic SC", cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navBar_container {
  display: flex;
  max-width: 100%;
  align-items: center;
  height: 11vh;
  background-color: #327d11;
}

.navBar_logoh {
  width: 30%;
}

.navBarLogo {
  height: 8vh;
  padding-left: 3em;
}

.navBar_links {
  display: flex;
  width: 70%;
  justify-content: space-evenly;
}

nav ul li {
  display: inline-flex;
  margin: 0;
}

nav ul li a {
  text-decoration: none;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 1.3rem;
  color: #e7ecef;
  text-transform: uppercase;
}

nav ul li a:active {
  font-weight: 700;
  color: brown;
}

